.slide {
  transform: translateX(100%);
  transition: all 1s ease;
}

.slide.active {
  transition: all 1s ease;
  transform: translate(0);
  animation: slide-out 1s ease 8.7s;
}

@keyframes slide-out {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-100%);
  }
}
